import 'bootstrap/dist/css/bootstrap.min.css'; //bootstrap
import { useState } from 'react';
import {Form, Button} from 'react-bootstrap';
import {useNavigate} from 'react-router-dom';
import axios from 'axios';

function Login(){
    let navigate = useNavigate();
    let [login,setLogin] = useState({
        email:'',
        pwd:''
    })
    const handleChange = (e)=>{
        setLogin({...login,[e.target.name]:e.target.value})
    }
    return (
        <>
        <div className='signupContainer'>
            <h1></h1>
            <Form className='loginBox'>
            <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Control type="email" placeholder=" " name='email' value={login.email} onChange={handleChange}/>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Control type="password" placeholder="" name='pwd' value={login.pwd} onChange={handleChange}/>
            </Form.Group>
            </Form>

            <Button variant="outline-success" onClick={()=>{
                axios.post('/login',login).then((result)=>{
                    if(result.data==''){
                        alert('')
                        navigate('/')
                    }
                })
            }}></Button>
            <Button variant="outline-success" onClick={()=>navigate('/signup')} style={{marginLeft: '40px'}}></Button>
        </div>
        </>
    )
}

export default Login
