import 'bootstrap/dist/css/bootstrap.min.css'; //bootstrap
import {Nav, Navbar, Container,Button,Form} from 'react-bootstrap';
import {useNavigate,useParams} from 'react-router-dom';
import axios from 'axios';
import  {useState} from 'react'

function Modify(props){
    let navigate = useNavigate()
    let {id} = useParams();  
    
    let [info,setInfo] = useState({
        id: props.user.id,
        email:'',
        pwd:'',
        names:'',
        birthday:'',
        telnum:'',
        gender:''
    })
    const handleChange = (e)=>{
        setInfo({...info,[e.target.name]:e.target.value})
    }
    return (
        <>
        <div className='signupContainer'>
            <h1> </h1>

            <Form className='loginBox'>
            <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Control type="email" placeholder=" " name='email' value={info.email} onChange={handleChange}/>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Control type="password" placeholder="" name='pwd' value={info.pwd} onChange={handleChange}/>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}> </Form.Label>
                <Form.Control type="password" placeholder=" " />
            </Form.Group> 

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Control type="text" placeholder="" name='names' value={info.names} onChange={handleChange}/>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Control type="date" placeholder="" name='birthday' value={info.birthday} onChange={handleChange}/>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Control type="text" placeholder="" name='telnum' value={info.telnum} onChange={handleChange}/>
            </Form.Group>

            <Form.Group className="mb-3">
                <Form.Label style={{fontSize:'20px', fontWeight:'bold'}}></Form.Label>
                <Form.Select name='gender' onChange={handleChange}>
                    <option value='' selected></option>
                    <option value=''></option>
                </Form.Select>
            </Form.Group>

            <Button variant="outline-success" onClick={()=>{
                axios.post('/modify',info).then((result)=>{
                    console.log('')
                    alert("!")
                    navigate('/mypage/'+id)
                    
                })
                .catch(()=>{
                    console.log('')
                })
            }}></Button>
        </Form>
        </div>
        </>
    )
}

export default Modify